* {
	box-sizing: border-box;
}

body {
	background: $softBgColor;
}




// =========================HEADER STYLES==================


header {
	width: 100%;
	background-color: $darkGrey;
	overflow: hidden;

	.header-image-wrap {
		padding: 20px;
		float: left;
		width: 50%;

		img {
			padding: 20px;
			width: 100%;
		}
	}

		.hamburger {
			float: right;
			margin: 20px;
			padding: 15px;
			cursor: pointer;
			z-index: 10;

			.line-1 {
				width: 45px;
				height: 3px;
				border-radius: 10px;
				background-color: white;
				margin: 6px 0;
				transition: 0.4s; 
			}

			.line-2 {
				width: 45px;
				height: 3px;
				border-radius: 10px;
				background-color: white;
				margin: 6px 0;
				transition: 0.4s;
			}
			
			.line-3 {
				width: 45px;
				height: 3px;
				border-radius: 10px;
				background-color: white;
				margin: 6px 0;
				transition: 0.4s ease-in-out;
			}

			.change.line-1 {
				transform: rotate(-45deg) translate(-5px, 3px);
				width: 45px;
			}
	
			.change.line-2 {
				opacity: 0;
			}
	
			.change.line-3 {
				transform: rotate(45deg) translate(-8px, -8px) ;
			}
	}

	.toggle-nav {
		display: none;
	}

	nav {
		width: 100%;
		background-color: rgba(108, 109, 112, .5);
		clear: both;

		
		ul {
			width: 60%;
			margin: 0px auto;
			padding: 10px;
			display: flex;
			flex-direction: row;
			justify-content: center;
			flex-wrap: wrap;
			

			li {
				margin: 10px;	
			}

			.divider {
				position: relative;
			}

			.divider::after {
				content: "";
				position: absolute;
				height: 12px;
				width: 2px;
				background-color: $textColor;
				top: 20%;
				left: 120%;
			}

			.divider-estate {
				position: relative;
			}

			.divider-estate::after {
				content: "";
				position: absolute;
				height: 12px;
				width: 2px;
				background-color: $textColor;
				top: 20%;
				left: 112%;
			}

			.divider-banking {
				position: relative;
			}

			.divider-banking::after {
				content: "";
				position: absolute;
				height: 12px;
				width: 2px;
				background-color: $textColor;
				top: 20%;
				left: 115%;
			}

			.divider-oil {
				position: relative;
			}

			.divider-oil::after {
				content: "";
				position: absolute;
				height: 12px;
				width: 2px;
				background-color: $textColor;
				top: 20%;
				left: 112%;
			}
			
			a {
				text-decoration: none;
				color: $textColor;
				font-size: 1.1em;
				margin: 0px auto;
			}
		}
	}
}






// ==========================HOME STYLES=================




.hero-home {
	width: 100%;	
	margin: auto;
	background-color: #8b8d90;
	background: url("/dist/img/placeholder.webp") no-repeat center;
	background-size: cover;
	height: 100vh;
	position: relative;


	.text-wrap-home {
		padding: 50px;
		p {
			width: 90%;
			font-size: 1.3em;
			padding: 10px;
			margin: 50px 20px;
			position: absolute;
			top: 14%;
			left: 0%;
			text-align: center;
			line-height: 1.5;
			color: $textColor;
			font-family: "TiemposTex-Regular";
		}	
	}

	
}

// ==========================ABOUT STYLES=================

.content-about {
	width: 100%;
	max-width: 1200px;
	padding: 20px;
	margin: auto;
	background-color: $softBgColor;

	.about-blurb {
		text-align: center;
		padding: 20px;
		line-height: 1.3;
	}

	h1 {
		border-bottom: 1px solid $darkGrey;
		text-align: center;
		padding: 10px;
		font-size: 2em;
		color: $darkGrey;
	}

	.col-1-about, .col-2-about, .col-3-about {
		margin-top: 25px;
		text-align: center;
		font-family: "TiemposTex-Regular";

		img {
			margin: 20px;
		}

		h2 {
			margin-bottom: 15px;
			font-size: 1.4em;
			color: $darkGrey;
		}

		p {
			font-size: 1.1em;
			margin: 15px 0px;
			line-height: 1.2;
			text-align: left;
		}
	}
}


