// ==========================TEAM STYLES=================


.content-team {
	width: 100%;
	max-width: 1200px;
	padding: 20px;
	margin: auto;
	background-color: $softBgColor;

	h1 {
		border-bottom: 1px solid $darkGrey;
		text-align: center;
		padding: 10px;
		font-size: 2em;
		color: $darkGrey;
	}

	.col-1-team, .col-2-team, .col-3-team {
		margin-top: 25px;
		text-align: left;
		line-height: 1.2;

		img {
			display: block;
			margin: auto;
			padding: 20px;
		}
		h2 {
			margin: 5px 0px;
			display: inline-block;
			font-size: 1.1em;

		}

		.divider-team {
			position: relative;
		}

		.divider-team::after {
			content: "";
			position: absolute;
			height: 10px;
			width: 2px;
			background-color: $darkGrey;
			top: 2%;
			left: -6%;
		}

		h3 {
			margin: 5px 10px;
			display: inline-block;
			vertical-align: baseline;
			font-size: .7em;
		}
	}
}

